@import url(https://fonts.googleapis.com/css?family=Ubuntu);
header {
  background-color: lightgrey;
  margin: 0;
  padding: 10px;
}
h1 {
  padding-left: 10px;
}

nav {
  padding-left: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

a {
  margin: 0 10px;
}




.App {
  background-color: lightslategrey;
  min-height: 100vh;
}
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --font-size: calc(10px + 1vmin);
}

a {
  text-decoration: none;
  color: white;
}

section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: darkgrey;
  border-radius: 5px;
  margin: 10px;
  padding: 20px;
}

form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 10px;
}

form > * {
  margin: 5px;
}

ul {
  list-style: none;
  margin-left: 0;
}

.container {
  background-color: rgb(207, 207, 207);
  margin: 3px;
  padding: 20px;
  border-radius: 10px;
  color: rgb(70, 70, 70);
}

.word-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.word { 
  display: -webkit-flex; 
  display: flex;
  color: rgb(189, 189, 189);
  font-weight: bolder;
  text-align: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 27vw;
  padding: 0 30px;
  margin: 5px;
  border-radius: 10px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  background-color: rgb(125, 125, 125);
}

.foreign {
  color: whitesmoke;
  text-align: center;
  font-size: 30px;
}

